<script>
import firebase from "firebase";
import { ref } from "vue";
import Swal from "sweetalert2";
import { useRouter } from "vue-router";
export default {
  name: "Signup",

  setup() {
    let router = useRouter();
    let ViewModel = ref({
      email: "",
      password: "",
      passwordConfirmation: "",
    });
    let errorMessage = ref("");
    let successMessage = ref("");
    let xhrRequest = ref(false);
    const teste = (e) => {
      console.log(e);
    };
    const signup = () => {
      if (ViewModel.value.password != ViewModel.value.passwordConfirmation) {
        errorMessage.value = "As senhas não conferem";
        console.log("As senhas não conferem");
      } else if (ViewModel.value.password.length < 6) {
        errorMessage.value = "A senha deve ter no minimo 6 caracteres";
        console.log("A senha deve ter no minimo 6 caracteres");
      } else {
        xhrRequest.value = true;
        firebase
          .auth()
          .createUserWithEmailAndPassword(
            ViewModel.value.email,
            ViewModel.value.password
          )
          .then(
            () => {
              xhrRequest.value = false;
              //   v.successMessage = "Register Successfully.";
              //   v.xhrRequest = false;
              let apiEmail = document.getElementById("sib-form");
              apiEmail.submit();
              alert("travar");

              Swal.fire({
                icon: "success",
                title: `Sucesso`,
                text: `O e-mail "${ViewModel.value.email}" foi cadastre-se com sucesso`,
                confirmButtonText: `Entrar`,
              }).then(() => {
                firebase
                  .auth()
                  .signInWithEmailAndPassword(
                    ViewModel.value.email,
                    ViewModel.value.password
                  )
                  .then(
                    (resp) => {
                      router.push("/curso/sitedo0");
                      console.log("resp", resp.user.refreshToken);
                      "AutenticacaoCanalCodigoFrontEnd",
                        localStorage.setItem(resp.user.refreshToken);
                    },
                    (error) => {
                      console.log("erro", error.message);
                    }
                  );
                // if (response.isConfirmed) {

                // }
              });
              console.log("Register Successfully");
            },
            (error) => {
              //   let errorResponse = JSON.parse(error.message);
              //   v.errorMessage = errorResponse.error.message;
              //   v.xhrRequest = false;
              Swal.fire({
                icon: "warning",
                title: `Atenção`,
                text: `${error.message}`,
                confirmButtonText: `Fechar`,
              });
              console.log("erro", error.message);
            }
          );
      }
    };
        const mostrarSenha = () => {
      let inputPassword = document.getElementById("password");
      let inputpasswordConfirmation = document.getElementById("ConfirmPassword");

      if (inputPassword.getAttribute("type") == "password") {
        inputPassword.setAttribute("type", "text");
        inputpasswordConfirmation.setAttribute("type", "text");
      } else {
        inputPassword.setAttribute("type", "password");
        inputpasswordConfirmation.setAttribute("type", "password");
      }
    };
    return {
      ViewModel,
      signup,
      errorMessage,
      successMessage,
      xhrRequest,
      teste,
      mostrarSenha
    };
  },
  methods: {
    signupRequest() {
      let v = this;
      v.xhrRequest = true;
      v.errorMessage = "";
      v.successMessage = "";

      firebase
        .auth()
        .createUserWithEmailAndPassword(v.email, v.password)
        .then(
          () => {
            v.successMessage = "Register Successfully.";
            v.xhrRequest = false;
          },
          (error) => {
            let errorResponse = JSON.parse(error.message);
            v.errorMessage = errorResponse.error.message;
            v.xhrRequest = false;
          }
        );
    },
  },
};
</script>
<style scoped>
